.footer {
    margin-top: 80px;
    padding-bottom: 4px;
  }
  .footer-link-text {color: #b2adad;}

  .footer {
    position: fixed;
    left: 50%;
    bottom: 0;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 5px;
    background-color: #f1f1f1; 
    z-index: 1000; 
  }
  [data-theme="dark"] .footer{
    background-color: #202020;
  }
  
  @media screen and (min-width: 768px) {
    .footer {
      margin-top: 100px;
    }
  }
  
  .for-footer {
    @media screen and (min-width: 768px) {
      display: flex;
      justify-content: center;
      align-items: center;
    }
  }
  
  .footer-text {
    color: #b2adad;
    font-size: 14px;
    line-height: calc(16.41 / 14);
    text-align: center;
    
  
    @media screen and (min-width: 768px) {
      font-size: 16px;
      line-height: calc(18.75 / 16);
    }
  }
  
  .footer-heart {
    fill: var(--green);
    animation: beat 340ms infinite alternate;
    margin-left: 10px;
    margin-right: 10px;
    width: 18px;
    display: inherit;
  }
  
  @keyframes beat {
    to {
      transform: scale(1.25);
    }
  }
  
  .footer-text-for-flex {
    display: flex;
    justify-content: center;
    align-items: center;
  }
  
  .footer-text {
    position: relative;
    
  }
  .footer-text-modal {
    cursor: pointer;
  }
  .footer-link span {
    color: #545454;
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  }

  .footer-text-modal {
    user-select: none;
    border: none;
    cursor: pointer;
    background: transparent;
  }
  .footer-text-modal::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: var(--green); 
    transform: scaleX(0);
    transition: transform 0.3s ease-in-out;
  }
  .footer-text-modal:hover::before,
  .footer-text-modal:focus::before {
    transform: scaleX(1);
  }


