* {
  font-family: Inter;
  box-sizing: border-box;

  list-style: none;
  text-decoration: none;
}

.hero {
  /* centrat */
  margin: 50px auto;
  max-width: 1280px;
overflow: hidden;
 //adaugat aici margin pt a testa butonul go up !!!!!!!!!!!!!!
margin-bottom: 70px;
}

.hero-container {
  display: flex;
  flex-direction: row;
  gap: 32px;
  padding: 40px;
  justify-content: center;
}

.hero-left-side {
  padding-top: 64px;
  display: flex;
  flex-direction: column;
  gap: 32px;
}


.hero-text-container {
  display: flex;
  flex-direction: column;
  gap: 16px;
}

.hero-title {
  color: #050505;
  font-family: Inter;
  font-size: 36px;
  font-style: normal;
  font-weight: 600;
  line-height: 120%; 
  text-transform: uppercase;

  max-width: 446px;
}

.hero-span {
  color: #9bb537;
  font-family: Inter;
  font-size: 36px;
  font-style: italic;
  font-weight: 400;
  line-height: 120%;
  text-transform: uppercase;
}

.hero-information {
  color: #050505;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;

  max-width: 408px;
}

.hero-button {
  display: inline-flex;
  padding: 14px 28px;
  justify-content: center;
  align-items: center;
justify-content: center;
  border-radius: 15px;
  border: 1px solid #9bb537;

  color: #050505;
  font-family: Inter;
  font-size: 13px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;

  max-width: 127px;
}

.hero-button:hover,
.hero-button:focus {
  transition: all 0.5s;
  border-radius: 15px;
  background: #9bb537;
  color: #f8f8f8;
}

/* SLIDER */

.slide-wrapper {
position: relative;
max-width: 702px;


}

.slider {
display: flex;
gap: 16px;
overflow-x: hidden;

}

.slide {
max-width: 702px;
display: flex;
flex-direction: row;
gap: 16px;
transition: 1s;
}

