  background: #9bb537;
  color: #f8f8f8;
}

[data-theme="dark"] .auto-nav- div {
  border: 1px solid #fff;
  background: #fff;
}

[data-theme="dark"] .content {
  background: var(--background-d);
}

[data-theme="dark"] .exit-button > svg {
  stroke: #fff;
}

[data-theme="dark"] .form-title {
