//Categories
.left-collumn {
    display: flex;
    flex-direction: column;
  }
  .btn-all-categories {
    max-width: 176px;
  
    font-family: Inter;
    font-size: 18px;
  
    line-height: 1.2;
    color: var(--primary-color-05);
    background-color: var(--bg-main);
  
    padding: 14px 29px;
    border-radius: 15px;
    border: 1px solid var(--primary-color-02);
  
    margin-bottom: 24px;
  }
  
  .btn-all-categories:is(:hover, :focus) {
    color: var(--bg-main) !important;
    background-color: var(--accent);
    border-color: transparent;
  }
  .active_all-categories {
    color:  var(--white_2);;
    background-color: var(--accent);
    border-color: #0000;
  }
  .active {
    color: var(--accent) !important;
  }
  
  .category-item {
    cursor: pointer;
    font-family: Inter;
    font-size: 14px;
    line-height: 1.3;
    color: var(--primary-color-03);
  }
  
  .category-item:is(:hover, :focus) {
    color: var(--accent);
  }
  
  .scrollable-list {
    width: 100%;
    height: 198px;
    overflow: hidden;
    margin-bottom: 40px;
  }
  
  .categories-list {
    display: flex;
    flex-direction: column;
    gap: 18px;
    list-style: none;
    margin: 0;
    padding: 0;
    overflow-y: scroll;
    height: 100%;
    box-sizing: border-box;
    text-align: left;
  }
  
  //Filters
  
  .filter-time-area-mob {
    display: flex;
    gap: 5px;
    margin-bottom: 14px;
  }
  .select-text {
    font-size: 12px;
    line-height: 1.2;
    color: var(--primary-color-05);
    margin-bottom: 8px;
  }
  
  .select-text-mob {
    font-size: 12px;
    line-height: 1.2;
    color: var(--primary-color-05);
    margin-bottom: 8px;
  }
  
  .select-container {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-bottom: 14px;
    position: relative;
  }
  .select-container-tabl {
    display: none;
  }
  .select-container-mob {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    position: relative;
  }
  
  .select-container .input-filter {
    width: 310px;
    border-radius: 15px;
    padding: 14px 122px 14px 48px;
    border: 1px solid var(--primary-color-02);
    background-color: var(--bg-main);
    color: var(--primary-color-05);
  }
  
  .icon-search-input {
    position: absolute;
    fill: var(--primary-color-05);
    bottom: 16px;
    left: 18px;
  }
  
  .select-container-mob .area-select-mob {
    border-radius: 15px;
    width: 150px;
    padding: 14px 18px;
    border: 1px solid var(--primary-color-02);
    background-color: var(--bg-main);
    color: var(--primary-color-05);
  }
  
  .select-container-mob .time-select-mob {
    border-radius: 15px;
    width: 150px;
    padding: 14px 18px;
    border: 1px solid var(--primary-color-02);
    background-color: var(--bg-main);
    color: var(--primary-color-05);
  }
  
  .select-container .ingredients-select {
    border-radius: 15px;
    width: 188px;
    padding: 14px 18px;
    border: 1px solid var(--primary-color-02);
    background-color: var(--bg-main);
    color: var(--primary-color-05);
  }
  
  .reset-filter {
    cursor: pointer;
    display: flex;
    align-items: center;
    margin-left: auto;
    gap: 4px;
    color: var(--primary-color-05);
    font-size: 12px;
    line-height: 1.3;
    margin-top: 14px;
    margin-bottom: 20px;
    justify-content: flex-end;
  }
  
  .right-collumn {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  @media screen and (min-width: 375px) {
    .filter-time-area-mob {
      gap: 5px;
    }
    .select-container-mob .area-select-mob {
      width: 160px;
    }
  
    .select-container-mob .time-select-mob {
      width: 160px;
    }
    .filter-time-area-mob {
      gap: 14px;
    }
  }
  .option-text {
    background-color: var(--option-color);
    color: var(--primary-color-03);
  }
  
  .option-text:is(:hover, :focus) {
    color: --txt-color;
  }
  @media screen and (min-width: 768px) {
    //Categories
    .btn-all-categories {
      margin-bottom: 30px;
    }
  
    .scrollable-list {
      height: 416px;
    }
    .categories-list {
      gap: 24px;
    }
  
    // Filters
    .filter-time-area-mob {
      display: none;
    }
    .container-filters {
      display: flex;
      flex-wrap: wrap;
      gap: 16px;
    }
  
    .select-container-tabl {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      margin-bottom: 14px;
      position: relative;
    }
  
    .select-text {
      font-size: 14px;
      line-height: 1.3;
    }
    .select-text-tabl {
      color: var(--primary-color-05);
      margin-bottom: 8px;
      font-size: 14px;
      line-height: 1.3;
    }
  
    .select-container .input-filter {
      width: 278px;
    }
  
    .select-container-tabl .area-select {
      border-radius: 15px;
      padding: 14px 18px;
      border: 1px solid var(--primary-color-02);
      background-color: var(--bg-main);
      color: var(--primary-color-05);
      width: 141px;
    }
  
    .select-container-tabl .time-select {
      width: 125px;
      padding: 14px 18px;
      border-radius: 15px;
      border: 1px solid var(--primary-color-02);
      background-color: var(--bg-main);
      color: var(--primary-color-05);
    }
  
    .select-container .ingredients-select {
      width: 188px;
    }
  
    .reset-filter {
      margin-bottom: 14px;
      margin-right: 14px;
    }
  
    .reset-filter {
      margin-bottom: 14px;
    }
  }
  @media screen and (min-width: 1280px) {
    .btn-all-categories {
      margin-bottom: 42px;
    }
    .scrollable-list {
      margin-bottom: 64px;
    }
    .container-filters {
      display: flex;
      gap: 16px;
    }
    
   
  }
  .rating-body{
    fill: rgba(248, 248, 248, .1);
  }
  
  .container-filters{
    margin-top: -23px;
  }
  
  