  font-size: 1em;
}

small {
  font-size: 80%;
}

sub, sup {
  vertical-align: baseline;
  font-size: 75%;
  line-height: 0;
  position: relative;
}

sub {
  bottom: -.25em;
}

sup {
  top: -.5em;
}

table {
  text-indent: 0;
  border-color: inherit;
}

button, input, optgroup, select, textarea {
  margin: 0;
  font-family: inherit;
  font-size: 100%;
  line-height: 1.15;
}

button, select {
  text-transform: none;
}

button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

:-moz-ui-invalid {
  box-shadow: none;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}

[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

summary {
  display: list-item;
}

.rating-backdrop {
  z-index: 100;
  width: 100%;
  height: 100%;
  background-color: var(--primary-modal-rating);
  transition: opacity .5s cubic-bezier(.4, 0, .2, 1), visibility .5s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.visible {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition-delay: .15s;
}

.rating-modal-container {
  z-index: 102;
  transition: all .5s cubic-bezier(.4, 0, .2, 1) .15s;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.rating-backdrop.visible .rating-modal-container {
  transition-delay: 0s;
  transform: scale(.75)translate(-65%, -65%);
}

.rating-modal {
  background: var(--option-color);
  width: 100%;
  max-width: 335px;
  z-index: 5;
  border-radius: 15px;
  padding: 20px;
  position: relative;
}

@media screen and (min-width: 768px) {
  .rating-modal {
    max-width: 424px;
    padding: 32px;
  }
}

.btn-close-rating {
  padding: 0;
  transition: all .5s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 14px;
  right: 14px;
}

@media screen and (min-width: 768px) {
  .btn-close-rating {
    top: 16px;
    right: 16px;
  }
}

button {
