.input-switcher[type="checkbox"] {
  height: 0;
  width: 0;
  visibility: hidden;
  display: none;
}

.input-label {
  cursor: pointer;
  text-indent: -9999px;
  width: 46px;
  height: 20px;
  background: #cecdcd;
  border-radius: 18px;
  display: block;
  position: relative;
}

.input-label:after {
  content: "";
  width: 18px;
  height: 18px;
  background: var(--white_2);
  border-radius: 18px;
  transition: all .3s;
  position: absolute;
  top: 1px;
  left: 1px;
}

.input-switcher:checked + .input-label {
  background: #9bb537;
  transition: all .3s;
}

.input-switcher:checked + .input-label:after {
  transition: all .3s;
  left: calc(100% - 1px);
  transform: translateX(-100%);
}

.input-label:active:after {
  width: 26px;
}

.menu-toggle {
