.tui-ico-first, .tui-ico-prev {
  font-size: 0;
}

.hide {
  display: none;
}

.show {
  display: flex;
}

.minimize {
  width: 14px;
  height: 14px;
  border-color: rgba(0, 0, 0, 0);
}

.footer-modal-echipa {
  width: 100%;
  height: 100%;
  z-index: 1010;
  background: rgba(0, 0, 0, .7);
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  overflow: auto;
}

.modal-content-footer-echipa {
  width: 768px;
  z-index: 1011;
  background: #fff;
  border-radius: 15px;
  flex-direction: column;
  gap: 20px;
  padding: 30px 15px 15px;
  transition: all .3s ease-in-out;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%)scale(1);
}

.close-footer {
  width: 24px;
  height: 24px;
  cursor: pointer;
  background: none;
  border: none;
  justify-content: center;
  align-items: center;
  transition: background-color .4s linear, transform .4s ease-in-out;
  display: flex;
  position: absolute;
  top: 15px;
  right: 15px;
}

.close-footer:hover, .close-footer:focus {
  transform: scale(1.5)rotate(90deg);
}

.close-footer > svg {
  stroke: #050505;
  width: 24px;
  height: 24px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.footer-modal-echipa-header {
  color: #050505;
  text-transform: uppercase;
  font-family: Inter;
  font-size: 18px;
  font-style: normal;
  font-weight: 600;
  line-height: 22px;
}

.footer-modal-echipa-text {
  color: #050505;
  max-width: 768px;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 24px;
}

.modal-footer-name {
  color: #050505;
  text-transform: uppercase;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
}

.modal-footer-role {
  color: #050505;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
}

.footer-modal-echipa-list {
  flex-flow: wrap;
  justify-content: center;
  align-items: center;
  gap: 7px;
  display: flex;
}

.footer-modal-echipa-container {
  width: 170px;
  background-color: #9bb537;
  border-radius: 10px;
  flex-direction: column;
  align-items: center;
  gap: 5px;
  padding: 10px;
  display: flex;
}

.modal-footer-picture {
  width: 120px;
  height: 120px;
  background-repeat: no-repeat;
  background-size: cover;
  border: 2px solid #161616;
}

.svg-footer-echipa {
  fill: #050505;
}

.cristina-footer {
  background-image: url("cristina.5691a3fd.jpeg");
}

.aimee-footer {
  background-image: url("aimee.3cbe6202.jpg");
}

.catalin-footer {
  background-image: url("catalin.c59ee9fa.jpg");
}

.craciun-footer {
  background-image: url("craciun.5d4bee81.jpg");
}

.ramon-footer {
  background-image: url("ramon.0f1d9e66.jpeg");
  background-position: center;
}

.george-footer {
  background-image: url("blanc-profile.8767d4a2.png");
  background-position: center;
}

.alina-footer {
  background-image: url("alina.c994f917.jpeg");
  background-position: center;
}

.modal-footer-svgs {
  flex-direction: row;
  gap: 10px;
  display: flex;
}

.modal-footer-link > svg {
  fill: #000;
}

@media screen and (min-width: 425px) and (max-width: 767px) {
  .modal-content-footer-echipa {
    width: 426px;
    transform: translate(-50%, -50%)scale(.6);
  }
}

@media screen and (max-width: 424px) {
  .modal-content-footer-echipa {
    width: 426px;
    transform: translate(-50%, -50%)scale(.5);
  }
}

[data-theme="dark"] .modal-content-footer-echipa {
  background: #161616;
}

[data-theme="dark"] .close-footer > svg {
  stroke: #fff;
}

[data-theme="dark"] [data-theme="dark"] .footer-modal-echipa-header, [data-theme="dark"] .footer-modal-echipa-text {
  color: #fff;
}

[data-theme="dark"] .modal-footer-picture {
  border: 2px solid #fff;
}

[data-theme="dark"] .modal-footer-name, [data-theme="dark"] .modal-footer-role {
  color: #fff;
}

[data-theme="dark"] .modal-footer-link > svg {
  fill: #fff;
}

[data-theme="dark"] .reset-filter {
  background-color: #161616;
}

