  --primary-color-08: rgba(5, 5, 5, .8);
  --primary-color-05: rgba(5, 5, 5, .5);
  --primary-color-03: rgba(5, 5, 5, .3);
  --primary-color-02: rgba(5, 5, 5, .2);
  --primary-color-01: rgba(5, 5, 5, .1);
