  --oragne: #eea10c;
  --primary-color-08: rgba(255, 255, 255, .8);
  --primary-color-05: rgba(255, 255, 255, .5);
  --primary-color-03: rgba(255, 255, 255, .3);
  --primary-color-02: rgba(255, 255, 255, .2);
  --primary-color-01: rgba(255, 255, 255, .1);
  --primary-rating: rgba(255, 255, 255, .5);
  --primary-modal-rating: rgba(255, 255, 255, .5);
  --primary--stars: rgba(255, 255, 255, .1);
  --primary-CardTitle-Description: rgba(255, 255, 255, .8);
  --primary-menu-mob: #f8f8f8;
  --primary-raiting: #1e1e1e;
  --border-pag: #9bb537;
  --option-color: #050505;
  --grey: rgba(255, 255, 255, .5);
  --modal-bg: #050505;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: var(--bg-main);
